.heading-1 {
  margin: 0;
  height: 42px;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
}
.image-4-icon {
  width: 189px;
  height: 185px;
  position: relative;
  object-fit: cover;
}
.image-4-wrapper {
  flex-direction: column;
  padding: var(--padding-3xs);
}
.heading-1-recipezy-parent,
.image-4-wrapper,
.image-content {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.image-content {
  flex-direction: row;
  padding: 0 var(--padding-base) 0 var(--padding-xs);
}
.heading-1-recipezy-parent {
  flex-direction: column;
  gap: var(--gap-3xs);
}
.main-content {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-mini-3) var(--padding-4xl);
}
.monkey-business,
.we-are-serious {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  font-weight: 700;
  font-family: inherit;
}
.main-content-parent {
  width: 424px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  max-width: 100%;
}
.frame-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.jump-in-to {
  position: relative;
  line-height: 23.5px;
}
.jump-in-to-have-fun-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-2xl) 0 var(--padding-xl);
}
.our-apps-are {
  position: relative;
  line-height: 23.5px;
}
.frame-container,
.frame-group {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frame-group {
  width: 513px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-xl);
}
.frame-container {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-2xl) 0 var(--padding-xl);
  box-sizing: border-box;
  font-size: var(--font-size-lg);
}
.group-icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.app-store-badgesvg-fill {
  position: absolute;
  top: 55px;
  left: 25px;
  width: 150px;
  height: 70.1px;
  overflow: hidden;
}
.group-parent {
  align-self: stretch;
  height: 55px;
  position: relative;
}
.frame-wrapper1 {
  width: 175px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-5xs-8);
  box-sizing: border-box;
}
.link-google-play-badgepng {
  height: 71.8px;
  width: 199px;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-div,
.frame-parent1 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  max-width: 100%;
}
.frame-parent1 {
  align-items: flex-end;
  gap: var(--gap-8xs-6);
}
.frame-div {
  align-self: stretch;
  align-items: flex-start;
  padding: 0 var(--padding-xl) 0 var(--padding-2xl);
  box-sizing: border-box;
}
.image-7-icon,
.image-8-icon {
  position: absolute;
  object-fit: cover;
}
.image-8-icon {
  height: 415px;
  width: 276px;
  margin: 0 !important;
  right: 58px;
  bottom: -176.9px;
  border-radius: var(--br-17xl);
}
.image-7-icon {
  top: 160.5px;
  left: 76px;
  border-radius: var(--br-15xl);
  width: 260px;
  height: 338px;
}
.app-showcasepng {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  background-image: url(./public/app-showcasepng@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.image-6-icon {
  height: 492px;
  width: 309px;
  position: relative;
  border-radius: var(--br-23xl);
  object-fit: cover;
  z-index: 1;
}
.showcase-image {
  align-self: stretch;
  height: 475.6px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-xl-6) var(--padding-xl) 0;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}
.frame-parent,
.monkeyappsio-inner {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frame-parent {
  width: 1152px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-23xl-8);
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-black);
  font-family: var(--font-dm-sans);
}
.monkeyappsio-inner {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
}
.copyright-2024 {
  align-self: stretch;
  position: relative;
}
.copyright-2024-monkey-apps-wrapper {
  width: 271px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-110xl) 0 0;
  box-sizing: border-box;
}
.send-us-a {
  position: relative;
}
.link-icon,
.link-icon-twitterpng {
  position: absolute;
  top: 0;
  max-height: 100%;
  object-fit: cover;
}
.link-icon {
  height: 100%;
  bottom: 0;
  left: 0;
  width: 54.3px;
}
.link-icon-twitterpng {
  height: calc(100% - 5px);
  bottom: 5px;
  left: 54.2px;
  width: 50px;
  overflow: hidden;
  z-index: 1;
}
.link-parent {
  height: 55px;
  flex: 1;
  position: relative;
}
.contact-info-inner {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-38xl) 0 var(--padding-37xl);
}
.privacy-policy {
  text-decoration: none;
  position: relative;
  color: inherit;
  display: inline-block;
  min-width: 115px;
}
.privacy-policy-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-33xl) 0 var(--padding-32xl);
}
.terms-and-conditions {
  position: relative;
}
.contact-info,
.terms-and-conditions-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-lg) 0 var(--padding-mid);
}
.contact-info {
  width: 341.2px;
  flex-direction: column;
  padding: 0 var(--padding-104xl) 0 0;
  box-sizing: border-box;
  gap: var(--gap-3xs);
  max-width: 100%;
}
.image-9-icon {
  height: 95.3px;
  width: 100px;
  position: relative;
  object-fit: cover;
}
.image-9-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 var(--padding-mini) 0 var(--padding-lg);
}
.scan-to-give {
  align-self: stretch;
  height: 24px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  z-index: 2;
}
.frame-parent2 {
  width: 133px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-xl-7);
}
.footer,
.monkeyappsio {
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
}
.footer {
  align-self: stretch;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.24);
  background-color: var(--color-black);
  flex-direction: row;
  justify-content: space-between;
  padding: var(--padding-lgi-9) var(--padding-18xl) var(--padding-xl)
    var(--padding-4xl);
  max-width: 100%;
  gap: var(--gap-xl);
  z-index: 1;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-white);
  font-family: var(--font-dm-sans);
}
.monkeyappsio {
  width: 100%;
  position: relative;
  overflow-y: auto;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-46xl) 0 0;
  background-image: url(./public/monkeyappsio@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 850px) {
  .heading-1,
  .monkey-business,
  .we-are-serious {
    font-size: var(--font-size-7xl);
  }
  .footer {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 675px) {
  .frame-parent {
    gap: var(--gap-2xl);
  }
}
@media screen and (max-width: 450px) {
  .heading-1,
  .monkey-business,
  .we-are-serious {
    font-size: var(--font-size-lgi);
  }
  .frame-parent1 {
    flex-wrap: wrap;
  }
  .contact-info {
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
